<template>
	<view class="item">
		<view>
			<u-navbar :is-back="true" back-icon-color="white" back-text="返回" :back-text-style="{ color: 'white' }"  title="更改昵称" :background="bgColor" title-color="white" title-size="40" ></u-navbar>
		</view>
		
		<input v-model="form.nickname" class="nickname" :style="inputStyle" type="text" @click="inputClick()">
		
		<view class="text">别致的名字更容易让朋友记住你。</view>
		
		<view class="button" :style="buttonStyle" @click="buttonClick()">保存</view>
	</view>
</template>

<script>
	export default{
		data(){
			return{
				form:{
					nickname:''
				},
				inputStyle:'',
				buttonStyle:'',
				bgColor:{
				  "backgroundImage": "linear-gradient(45deg, #1E90FF, 	#BA55D3)"
				}
			}
		},
		onLoad() {
			uni.request({
				url:'http://localhost/users/getUserinfo',
				method:"GET",
				header:{
					token: uni.getStorageSync('token')
				},
				success: (e) => {
					console.log(e);
					if(e.data.data.nickName == null){
						this.form.nickname = "未设置";
					}else{
						this.form.nickname = e.data.data.nickName;
					}
				}
			});
		},
		methods:{
			inputClick(){
				this.inputStyle = 'border-bottom: 1px solid #BA55D3'
				this.buttonStyle = 'background-color: #1E90FF; color: white'
			},
			buttonClick(){
				uni.request({
					url:'http://localhost/users/updateNickname',
					method:"POST",
					header:{
						token: uni.getStorageSync('token')
					},
					data:{
						"nickName": this.form.nickname
					},
					success: (e) => {
						console.log(e);
						uni.showLoading({
							title: '正在保存'
						});				
						setTimeout(function () {
							//缓存服务器下发token
							uni.setStorageSync('token',e.data.data.token)
							uni.hideLoading();
							uni.navigateBack({
								
							})
						}, 1500);						
					}
				});
				
			}
		}
	}
</script>

<style scoped>
	.item{
		background-color: white;
		height: 600rpx;
	}
	.nickname{
		border-bottom: 1px solid #bfbfbf;
		width: 670rpx;
		padding-left: 30rpx;
		font-size: 35rpx;
		margin-top: 50rpx;
		margin-left: 25rpx;
		margin-bottom: 20rpx;
	}
	.text{
		margin-left: 30rpx;
		color: #bfbfbf;
	}
	.button{
		background-color: #dbdbdb;
		width: 200rpx;
		height: 75rpx;
		font-size: 32rpx;
		line-height: 75rpx;
		text-align: center;
		margin-left: 280rpx;
		border-radius: 15rpx;
		color: #8a8a8a;
		margin-top: 150rpx;
	}
</style>